import React from 'react';
import { Table } from 'semantic-ui-react';
import { floorMappingAll } from '../../../../utils/dataUtils';
import { NormalInput } from '../../../shared/fields/NormalInput';
import { TInputTypeObject } from './../utils/types/setupTypes';

type Floor = {
	floor: number;
	length: number;
	height: number;
	width: number;
	area: number;
};

type TProps = {
	floorArray: Floor[];
	inputType: TInputTypeObject | undefined;
};

export const FloorTable = ({ floorArray, inputType }: TProps) => {
	const headers = inputType && inputType.meta && inputType.meta.header;
	if (!headers) return null;
	return (
		<div>
			<Table collapsing size="small" compact="very">
				<Table.Header>
					<Table.Row>
						<Table.HeaderCell></Table.HeaderCell>
						{Object.values(headers).map((header) => (
							<Table.HeaderCell key={header}>{header}</Table.HeaderCell>
						))}
					</Table.Row>
				</Table.Header>
				<Table.Body>
					{floorArray.map((floor: { [key: string]: number }, idx) => (
						<Table.Row key={floor.floor}>
							<Table.Cell>{floorMappingAll.find((f) => floor.floor === f.floor)!.nepaliName}</Table.Cell>
							{Object.keys(headers).map((header) => (
								<Table.Cell key={`floor.${idx}.${header}`}>
									<NormalInput label="" name={`floor.${idx}.${header}`} />
								</Table.Cell>
							))}
						</Table.Row>
					))}
				</Table.Body>
			</Table>
		</div>
	);
};
