import React from 'react';
import { Button, Form, Table } from 'semantic-ui-react';
import { getOptionText } from '../../../utils/dataUtils';
import { setupTableProps } from '../../../utils/tableUtils';
import { IFormPermissions, TOption } from '../interfaces';

export const FormPermissionsTable = ({
	formPermissions,
	options,
	handleDeletePermission,
}: {
	formPermissions: IFormPermissions[];
	options: TOption[];
	handleDeletePermission: (id: string) => void;
}) => {
	return (
		<div>
			{formPermissions.length < 1 ? (
				<p style={{ textAlign: 'center' }}>Please add Approval Permissions</p>
			) : (
				<Table size="small" compact="very" striped celled textAlign="center" selectable>
					<Table.Header>
						<Table.Row>
							<Table.HeaderCell>User</Table.HeaderCell>
							<Table.HeaderCell>Approval</Table.HeaderCell>
							<Table.HeaderCell></Table.HeaderCell>
						</Table.Row>
					</Table.Header>
					<Table.Body>
						{formPermissions.map((row) => (
							<Table.Row>
								<Table.Cell>{getOptionText(row.userType, options)}</Table.Cell>
								<Table.Cell>{row.approvalStatus}</Table.Cell>
								<Form.Button
									icon="close"
									title="Remove"
									size="tiny"
									className="danger-text-button"
									onClick={() => handleDeletePermission(row.userType)}
								/>
							</Table.Row>
						))}
					</Table.Body>
				</Table>
			)}
		</div>
	);
};
