import { Field, FieldProps, getIn } from 'formik';
import React from 'react';
import { DropdownProps, Form, Label, Select } from 'semantic-ui-react';
import { TOption } from '../../dynamic-setup/interfaces';

type TProps = {
	label: string;
	name: string;
	onChange?: (event: React.SyntheticEvent<HTMLElement, Event>, data: DropdownProps) => void;
	compact?: boolean;
	needsZIndex?: boolean;
	options: TOption[];
	formValue?: string | number;
};

export const SelectInputV2 = ({ options, name, label, compact = false, needsZIndex = false, onChange, formValue }: TProps) => {
	let className = '';
	if (compact) className += 'compact ';
	if (needsZIndex) {
		className += 'zIndexSelect';
	} else {
		className += 'EbpsCustomSelect';
	}
	return (
		<Field>
			{({ form }: FieldProps) => {
				const error = getIn(form.errors, name);
				return (
					<Form.Field error={!!error}>
						<div className={className}>
							{label && <span>{label}</span>}
							<Select
								options={options}
								value={formValue || getIn(form.values, name)}
								name={name}
								onChange={onChange ? onChange : (e, { value }) => form.setFieldValue(name, value)}
							/>
						</div>
						{error && (
							<Label pointing prompt size="large">
								{error}
							</Label>
						)}
					</Form.Field>
				);
			}}
		</Field>
	);
};
