import React from 'react';
import { Button, Form, Table } from 'semantic-ui-react';
import { getOptionText } from '../../../utils/dataUtils';
import { setupTableProps } from '../../../utils/tableUtils';
import { IFormFieldList, IFormPermissions, TOption } from '../interfaces';
import { StatusLabel } from '../shared/StatusLabel';

export const FormFieldListTable = ({
	formFieldList,
	options,
	handleDelete,
	handleEdit,
}: {
	formFieldList: IFormFieldList[];
	options: TOption[];
	handleDelete: (id: number) => void;
	handleEdit: (id: number) => void;
}) => {
	return (
		<div>
			{formFieldList.length < 1 ? (
				<p style={{ textAlign: 'center' }}>Please Add Form Fields</p>
			) : (
				<Table size="small" compact="very" striped celled textAlign="center" selectable>
					<Table.Header>
						<Table.Row>
							<Table.HeaderCell>Name</Table.HeaderCell>
							<Table.HeaderCell>Placeholder</Table.HeaderCell>
							<Table.HeaderCell>Required</Table.HeaderCell>
							<Table.HeaderCell>ReadOnly</Table.HeaderCell>
							<Table.HeaderCell>Ebps Column</Table.HeaderCell>
							<Table.HeaderCell>Actions</Table.HeaderCell>
						</Table.Row>
					</Table.Header>
					<Table.Body>
						{formFieldList.map((row) => (
							<Table.Row>
								<Table.Cell>{row.name}</Table.Cell>
								<Table.Cell>{row.placeholder}</Table.Cell>
								<Table.Cell>
									<StatusLabel status={row.required} mode="yes-no" />
								</Table.Cell>
								<Table.Cell>
									<StatusLabel status={row.readOnly} mode="yes-no" />
								</Table.Cell>
								<Table.Cell>{row.ebpsColumnId}</Table.Cell>
								<Table.Cell>
									<Button
										icon="edit"
										className="primary-btn"
										size="tiny"
										title="Edit"
										compact
										onClick={() => handleEdit(row.id || 0)}
									/>
									<Button
										compact
										icon="close"
										title="Remove"
										size="tiny"
										className="danger-button"
										onClick={() => handleDelete(row.id || 0)}
									/>
								</Table.Cell>
							</Table.Row>
						))}
					</Table.Body>
				</Table>
			)}
		</div>
	);
};
