import { Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { Form, Segment } from 'semantic-ui-react';
import { EValidationDataTypes } from './utils/enums/inputEnums';
import { TInputTypeObject, TOutputFormat } from './utils/types/setupTypes';
import * as Yup from 'yup';
import { IFormFieldList, IVocabulary } from '../interfaces';
import { showErrorToast } from '../../../utils/functionUtils';
import {
	normalNepaliDate,
	validateNepaliDate,
	validateNormalNepaliDate,
	validateNullableNepaliDate,
	validateNumber,
	validateString,
} from '../../../utils/validationUtils';
import { SaveButtonValidation } from '../../shared/SaveButtonValidation';
import { RenderLayoutInput } from './renderers/RenderLayoutInput';

type TProps = {
	// data: string;
	// dataMapping: IFormFieldList[];
	inputTypes: TInputTypeObject[];
	postAction: (data: any) => void;
	output: TOutputFormat[];
	readOnlyFields: IFormFieldList[];
	loading: boolean;
	savePermission: boolean;
	applicationNo: string;
	prevData: any;
	vocabulary: IVocabulary[];
	// setOutput: Dispatch<SetStateAction<TOutputFormat[]>>;
};

const floorDummyData = [
	{ floor: 1, length: 12, height: 41, width: 52, area: 523 },
	{ floor: 2, length: 12, height: 41, width: 52, area: 523 },
];

export const LayoutRenderer = ({
	applicationNo,
	prevData,
	readOnlyFields,
	inputTypes,
	output,
	postAction,
	loading,
	savePermission,
	vocabulary,
}: TProps) => {
	const [appIdIndex, setAppIdIndex] = useState(0);
	useEffect(() => {
		const inputType = inputTypes.find((row) => row.columnName === 'application_no');
		if (inputType) {
			setAppIdIndex(inputType.id);
		}
	}, [inputTypes]);

	const generateSchema = () => {
		let schema = {};
		inputTypes!.forEach((inputType) => {
			const validation = inputType && inputType.meta && inputType.meta.validation;

			if (validation) {
				if (validation.required) {
					if (validation.dataType === EValidationDataTypes.STRING) {
						Object.assign(schema, {
							[inputType.id]: validateString,
						});
					} else if (validation.dataType === EValidationDataTypes.NUMBER) {
						Object.assign(schema, {
							[inputType.id]: validateNumber,
						});
					} else if (validation.dataType === EValidationDataTypes.NORMAL_DATE) {
						Object.assign(schema, {
							[inputType.id]: validateNormalNepaliDate,
						});
					} else if (validation.dataType === EValidationDataTypes.UNICODE_DATE) {
						Object.assign(schema, {
							[inputType.id]: validateNepaliDate,
						});
					}
				} else {
					if (validation.dataType === EValidationDataTypes.NUMBER) {
						Object.assign(schema, {
							[inputType.id]: validateNumber,
						});
					} else if (validation.dataType === EValidationDataTypes.NORMAL_DATE) {
						Object.assign(schema, {
							[inputType.id]: normalNepaliDate,
						});
					} else if (validation.dataType === EValidationDataTypes.UNICODE_DATE) {
						Object.assign(schema, {
							[inputType.id]: validateNullableNepaliDate,
						});
					}
				}
			}
		});
		return Yup.object().shape(schema);
	};

	return (
		<Formik
			initialValues={prevData}
			validationSchema={generateSchema()}
			onSubmit={async (values, { setSubmitting }) => {
				setSubmitting(true);

				let dataToSend = { ...values };
				if (appIdIndex) {
					dataToSend = { ...values, [appIdIndex]: applicationNo };
				}
				try {
					await postAction(dataToSend);
					// if (this.props.success && this.props.success.success) {
					// 	handleSuccess(checkError(this.props.prevData), this.props.parentProps, this.props.success);
					// }
					window.scrollTo(0, 0);
					setSubmitting(false);
				} catch (err) {
					setSubmitting(false);
					window.scrollTo(0, 0);
					console.log('Error', err);
					showErrorToast('Something went wrong !!');
				}
				// setTimeout(() => {
				console.log(JSON.stringify(values, null, 2));
				//   setSubmitting(false);
				// }, 400);
				setSubmitting(false);
			}}
		>
			{(props) => (
				<Form loading={props.isSubmitting || loading}>
					<Segment style={{ textAlign: 'left' }}>
						<div>
							{output &&
								output.map((row) => (
									<React.Fragment key={row.id}>
										{row.input ? (
											<RenderLayoutInput
												content={row.content}
												formikProps={props}
												inputTypes={inputTypes}
												vocabulary={vocabulary}
											/>
										) : (
											<div
												style={{
													display: row.inline ? 'inline-block' : 'inline',
												}}
												dangerouslySetInnerHTML={{ __html: row.content }}
											/>
										)}
									</React.Fragment>
								))}
							{/* {formattedData && (
                )} */}
							{/* <div dangerouslySetInnerHTML={{ __html: formattedData }} /> */}
							{/* <Button
								primary
								content="Save"
								onClick={(e) => {
									e.preventDefault();
									props.handleSubmit();
								}}
							/> */}
							<SaveButtonValidation
								errors={props.errors}
								formUrl={''}
								hasSavePermission={savePermission}
								prevData={{}}
								hasDeletePermission={false}
								isSaveDisabled={false}
								handleSubmit={props.handleSubmit}
								validateForm={props.validateForm}
							/>
						</div>
					</Segment>
				</Form>
			)}
		</Formik>
	);
};
